<template>
	<div id="viewport">
		<Background ref="background" :width="roomsWidth" :rooms="rooms"/>
		<div id="info">
			<h1>Level {{ level }}</h1>
		</div>
		<div id="sprite">
			<lottie id="runAnim" :options="runAnimOptions" :height="400" :width="400" v-on:animCreated="handleRunAnimation" class="animatedSprite" />
			<lottie id="staticAnim" :options="staticAnimOptions" :height="400" :width="400" v-on:animCreated="handleStaticAnimation" class="animatedSprite"  />
      <lottie id="slideAnim" :options="slideAnimOptions" :height="400" :width="400" v-on:animCreated="handleSlideAnimation" class="animatedSprite" />
      <lottie id="dashAnim" :options="dashAnimOptions" :height="400" :width="400" v-on:animCreated="handleDashAnimation" class="animatedSprite"  />
      <lottie id="waitAnim" :options="waitAnimOptions" :height="400" :width="400" v-on:animCreated="handleWaitAnimation" class="animatedSprite"  />
      <lottie id="jumpAnim" :options="jumpAnimOptions" :height="400" :width="400" v-on:animCreated="handleJumpAnimation" class="animatedSprite"  />
		</div>
		<div id="input" class="hide">
			<b-form inline>
	      <b-input d="inputField" placeholder="Saisir votre parcours" v-model="stringInput" />
	      <b-button variant="primary" v-on:click="startMove" class="startbt">Go</b-button>
    	</b-form>
	  	<p
	  		v-for="(input, inputKey) in userInput"
	  		:id="['input_' + inputKey]"
	  		class="input"
	  	>
	  	{{ input }}
	  	</p>
	  </div>
	  <div id="rooms" :style="{ width: roomsWidth + 'px', left: startOffset + 'px'}">
	  	<div
	  		v-for="(room, roomKey) in rooms"
	  		:id="['room_' + roomKey]"
	  		class="room"
	  		:style="{ left: room.position + 'px'}"
	  	>
	  	<p>{{ actions[room.actionId].name }}</p>
	  	</div>
	  </div>
	  <div id="bottomMenu">
		  <b-button id="start" :disabled="this.inProgress" variant="primary" v-on:click="startIntro" class="startbt">Démarrer</b-button>
		</div>
		<b-modal ref="modalChoices" no-close-on-esc no-close-on-backdrop hide-header-close id="modalChoice" title="Choisissez vos actions">
    	<b-button v-for="(action, actionKey) in actions" variant="primary"
    	v-on:click="chooseAction(action.id)" class="action" :key="action.id">
        <img  v-bind:src="action.asset" width="108" height="108">
      </b-button>
    	<div slot="modal-footer" class="w-100">
         <div v-for="(room, roomKey) in rooms" class="dot" :id="['dot_' + roomKey]" >
         </div>
         <b-button size="sm" class="float-right startbt" :disabled="this.cantTest" v-on:click="startMove" variant="primary">
          OK
         </b-button>
       </div>
  	</b-modal>
		<Foreground ref="foreground" :width="roomsWidth" />
		<div id="sunlight"></div>
	</div>
</template>

<script>

import Velocity from 'velocity-animate';
import Lottie from './lottie.vue';
import Background from './background.vue';
import Foreground from './foreground.vue';
import * as runAnim from '../assets/anim/run.json';
import * as staticAnim from '../assets/anim/static.json';
import * as slideAnim from '../assets/anim/slide.json';
import * as waitAnim from '../assets/anim/wait.json';
import * as dashAnim from '../assets/anim/dash.json';
import * as jumpAnim from '../assets/anim/jump.json';

export default {
  name: 'Main',
  components: {
     'lottie': Lottie,
	      'Background': Background,
			     'Foreground': Foreground
  },
  data () {
    return {
    	stringInput: '',
    	actions: [
    		{id:0, name:'Jump', anim:'jump',  asset:require('../assets/ui/1x/ICO_jump.png')},
    		{id:1, name:'Dash', anim:'dash', asset:require('../assets/ui/1x/ICO_dash.png')},
    		{id:2, name:'Slide', anim:'slide', asset:require('../assets/ui/1x/ICO_slide.png')},
    		{id:3, name:'Wait', anim:'wait', asset:require('../assets/ui/1x/ICO_wait.png')},
    	],
    	levels: [],
      rooms: [],
      userInput: [],
      roomWidth: 800,
      roomsWidth: 100000,
      roomCount: 0,
      inProgress: false,
      cantTest: true,
      runAnimOptions: {animationData: runAnim, autoplay: false},
      staticAnimOptions: {animationData: staticAnim, autoplay: false},
      dashAnimOptions: {animationData: dashAnim, autoplay: false},
      waitAnimOptions: {animationData: waitAnim, autoplay: false},
      slideAnimOptions: {animationData: slideAnim, autoplay: false},
      jumpAnimOptions: {animationData: jumpAnim, autoplay: false},
      animationSpeed: 1,
      level:1,
      startRoomNumber:2,
      totalLevel:10,
      startOffset:0,
      itvAnim: false
    }
  },
  watch: {
    // whenever stringInput changes, this function will run
    stringInput: function () {
      this.userInput = this.stringInput.split('');
      //handle dot light
      for (var i = 0; i < this.rooms.length; i++) {
      	if(i < this.userInput.length)
      		$('#dot_'+i).addClass('on');
      	else
      		$('#dot_'+i).removeClass('on');
      };
      //
      this.cantTest = !(this.userInput.length == this.rooms.length);
      if(this.userInput.length > this.rooms.length)
        this.stringInput = this.stringInput.substr(0, this.rooms.length);
    }
  },
  methods: {
  	reset:function() {
  		$(".room").removeClass('ok');
  		$(".room").removeClass('no');
  		this.userInput = [];
  		this.stringInput = '';
  		this.roomCount = 0;
  		this.$refs.modalChoices.hide();
  	},
  	win:function() {
  		if(this.level < this.totalLevel) {
  			this.level++;
  			this.rooms = this.levels[this.level - 1];
  			this.defineRoomPosition();
  			this.startOffset = -this.rooms[this.rooms.length - 1].position;
  			$('#sprite').removeClass('ready');
  			this.$refs.modalChoices.hide();
  		}
  	},
  	chooseAction: function(actionId) {
  		this.stringInput += ''+actionId;
  	},
  	handleRunAnimation: function (anim) {
      this.runAnim = anim;
    },
    handleStaticAnimation: function (anim) {
      this.staticAnim = anim;
    },
    handleWaitAnimation: function (anim) {
      this.waitAnim = anim;
    },
    handleSlideAnimation: function (anim) {
      this.slideAnim = anim;
    },
    handleDashAnimation: function (anim) {
      this.dashAnim = anim;
    },
    handleJumpAnimation: function (anim) {
      this.jumpAnim = anim;
    },
    setSpriteAnim: function (state) {
      clearTimeout(this.itvAnim);
      var anim = ['run', 'static', 'wait', 'dash', 'slide', 'jump'];
      for (var i = 0; i < anim.length; i++) {
        if(anim[i] == state) {
          this[anim[i]+'Anim'].play();
          $('#'+anim[i]+'Anim').removeClass('hide');
        }
        else {
          this[anim[i]+'Anim'].pause();
          $('#'+anim[i]+'Anim').addClass('hide');
        }
      };
    },
  	init: function() {
  		var scope = this;
  		this.rooms = this.levels[this.level - 1];
  		this.defineRoomPosition();
  		this.startOffset = -this.rooms[this.rooms.length - 1].position;
  	},
  	startIntro: function() {
  		$('#sprite').removeClass('ready');
  		if(this.userInput) {
  			this.reset();
  		}
  		var scope = this;
  		this.inProgress = true;
  		$('#start').css('display', 'none');
  		$("#rooms").velocity({ left: this.roomWidth+'px' },
  		{
  			duration: this.rooms.length*.5*2000,
        easing:'easeInOutSine',
        complete: function() {
        	scope.startTyping();
        },
				progress: function(elements, complete, remaining, start, tweenValue) {

				scope.backgroundParallax();
    }
  		});
  	},
		backgroundParallax:function(){
			this.$refs.background.slide($("#rooms").css('left').replace(/[^-\d\.]/g, ''));
			this.$refs.foreground.slide($("#rooms").css('left').replace(/[^-\d\.]/g, ''));
		},
  	startTyping: function() {
  		this.$refs.modalChoices.show();
  		$('#sprite').addClass('ready');
      this.setSpriteAnim('static');
  		this.inProgress = false;
  	},
  	defineRoomPosition: function() {
  		var totalPosition = 0;
  		for (var i = 0; i < this.rooms.length; i++) {
  			totalPosition += this.rooms[i].gap;
  			this.rooms[i].position = totalPosition;
  			this.rooms[i].id = i;
  			totalPosition += this.roomWidth;
  		};
  		//update rooms width
  		this.roomsWidth = totalPosition+1000;
  	},
  	startMove: function() {
  		this.$refs.modalChoices.hide();
  		var scope = this;
  		this.inProgress = this.cantTest = true;
			this.move();
			this.setSpriteAnim('run');
  	},
  	move: function() {

  		if(this.roomCount < this.rooms.length)
  		{
  			this.moveToRoom(this.rooms[this.roomCount]);
  			this.roomCount++;
  		}
  		else {
  			//end session
  			this.endMove();
  			this.win();
  		}
  	},
  	moveToRoom:function(room) {
  		var scope = this;
  		//to do: resolve issue with scroll hidden
  		var offset = room.position;
  		$("#rooms").velocity({ left: -offset+'px' },
  		{
  			duration: 2000 + room.gap,
        complete: function() {
        	scope.checkRoom(room);
        },
				progress: function(elements, complete, remaining, start, tweenValue) {
					scope.backgroundParallax();
    },
        easing:'linear',
  		});
  		//method with scroll
			/*$("#rooms").velocity('scroll', {
			 	offset: offset+'px',
  			duration: 2000+room.gap,
        complete: function() {
        	scope.checkRoom(room);
        },
        container:$('#viewport'),
        easing:'linear',
        axis: "x"
  		});*/
  	},
  	checkRoom: function(room) {
  		var scope = this;
      //if ok
  		if(room.actionId == this.userInput[room.id]) {
  			$('#room_'+room.id).addClass('ok');
  			$('#input_'+room.id).addClass('ok');
        if(room.anim != 'wait')
  			 scope.move();
        this.setSpriteAnim(room.anim);
        scope.itvAnim = setTimeout(function() {
          scope.setSpriteAnim('run');
          if(room.anim == 'wait')
            scope.move();
        }, 1000);
  		}
      //if error
  		else {
  			$('#room_'+room.id).addClass('no');
  			$('#input_'+room.id).addClass('no');
  			scope.endMove();
  		}
  		/*setTimeout(function() {
  			scope.move();
  		}, 1000);*/

  	},
  	endMove: function() {
  		this.inProgress = false;
  		$('#start').css('display', 'block');
  		this.setSpriteAnim('static');
  	},
  	generateLevels: function() {
  		for (var i = 1; i < this.totalLevel + 1; i++) {
  			var level = [];
  			for (var j = 0; j < this.startRoomNumber; j++) {
  				var actionId = Math.round(Math.random() * (this.actions.length - 1));
          //var gap = Math.round(Math.random() * 300);
  				var gap = this.roomWidth;
  				var room = {actionId: actionId, gap: gap, anim:this.actions[actionId].anim};
  				level.push(room);
  			};
  			this.levels.push(level);
  			this.startRoomNumber++;
  		};
  	}
  },
  created: function() {
  	this.generateLevels();
  	this.init();
  },
}
</script>

<style lang="scss">
  button.startbt {
    background: transparent url('../assets/ui/1x/ICO_ext_play.png') no-repeat top left!important;
    background-size: 108px 108px!important;
    width: 108px;
    height: 108px;
    border: 0;
    border-radius: 0;
    text-indent: -9999px;
    overflow: hidden;
  }
</style>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

	$roomWidth: 400px;

	#viewport {
		position: absolute;
		top: 0; left: 0;
		width: 100%;
		height: 100%;
		overflow: hidden;
		background-color: #52bf74;
		z-index: -9;
	}

	#info {
		position: absolute;
		top: 10px;
		right: 10px;
		width: 100%;
		h1 {
      font-size: 16px;
      width: 100%;
      text-align: right;
		}
	}

	#roomsContainer {
		position: absolute;
		top: 0; left: 0;
		width: 100%;
		height: 100%;
	}

	#rooms, #sprite {
		top: 50%;
		margin-top: -200px;
	}

	#rooms {
		position: absolute;
		left: $roomWidth;
		margin-right: 1000px;
	}

	#sprite {
		position: absolute;
		left: 0;
		z-index: 100;
		//border: 1px solid;
		width: $roomWidth;
		height: $roomWidth;
		opacity: 0;
		transition: opacity .5s ease-in-out;
	  z-index: -1;
    .animatedSprite {
      position: absolute;
    }
	}

	#sprite.ready {
		display:block;
		opacity: 1;
	}

	#start {
		position: fixed;
		bottom: 40px;
		left: 50%;
		transform: translateX(-50%);
	}

	.guy {
		position: relative;
		height: 60%;
		width: 10%;
		margin-top: 40%;
		background: black;
		margin-left: 20px;
	}

	#input {
		position: fixed;
		top: 60px;
		left: 50%;
		transform: translateX(-50%);
	}

	.hide {
		display: none;
	}

	.input {
		display: inline-block;
	}
	// .input.ok {
	// 	color: green;
	// }
	// .input.no {
	// 	color: red;
	// }

	.room {
		width: $roomWidth;
		height: $roomWidth;
		// background: #EEE;
		// border: 1px solid #000;
		position: absolute;
		p {
			font-size: 30px;
			text-align: center;
			width: 100%;
		}
	}

	#sunlight{
		position: absolute;
		top:0;
		bottom:0;
		left:0;
		right:0;
		z-index: -1;
		background-image: linear-gradient(to left bottom, rgba(#e8e8e8, .45), rgba(#7ABF8F, 0));
	}

	.room.no {
		background: red;
	}

	.dot {
		width: 10px;
		height: 10px;
		background: #FFFDC9;
		border-radius: 10px;
		display: inline-block;
		margin-right: 2px;
	}

	.dot.on {
		background: #452C00;
	}

	#modalChoice button {
		margin-right: 8px;
	}

  button.action {
    border-radius: 10px;
    background: #FFFDC9;
    border: 4px solid #452C00;
    height: 112px;
    width: 108px;
    box-shadow: 0 4px 0 0px #603700;
    transition: all .5s ease-in-out;
    padding: 0;
  }

  button.action:hover {
    box-shadow: 0 2px 0 0px #603700;
  }

</style>



